import React from 'react'
import './TutorialPreview.scss'

import { UnorderedList, ListItem } from 'carbon-components-react'
import { Time20, Download20 } from '@carbon/icons-react'
import allTutorials from '../tutorials_data.json'

const TopicsListItems = ({ topics }) => {
  const result = []

  if (topics && topics.length) {
    for (const topic of topics) {
      result.push(
        <ListItem className='tutorial-preview__' key={topic.title}>
          {topic.title}
        </ListItem>
      )
    }
  }

  return result
}

const OutcomesListItems = ({ outcomes }) => {
  const result = []

  if (outcomes && outcomes.length) {
    for (const outcome of outcomes) {
      result.push(
        <ListItem className='tutorial-preview__outcome' key={outcome.title}>
          {outcome.title}
        </ListItem>
      )
    }
  }

  return result
}

export const TutorialPreview = ({ tutorialName, downloadCount, tutorialData }) => {
  const tutorialDataLength = tutorialData && Object.keys(tutorialData).length
  const tutorial = tutorialDataLength > 0 ? tutorialData : allTutorials[tutorialName]
  const details = (tutorial && tutorial.tutorial && tutorial.tutorial.pageDetails) || {}
  const common = (tutorial && tutorial.tutorial && tutorial.tutorial._common) || {}
  const summary = details && details.summary

  return (
    <div className='tutorial-preview'>
      <div className='tutorial-preview__section'>
        <span />
        <p className='tutorial-preview__text'>{common && common.shortDescription}</p>
      </div>
      <div className='tutorial-preview__section tutorial-preview__section--row'>
        <div className='tutorial-preview__schedule' title='Estimated tutorial duration time'>
          <Time20 className='tutorial-preview__icon' />
          <p>{common && common.durationTime}.</p>
        </div>
        {
          downloadCount && (
            <div className='tutorial-preview__downloads' title='Number of downloads'>
              <Download20 className='tutorial-preview__icon' />
              <p>{downloadCount}</p>
            </div>
          )
        }
      </div>
      <div className='tutorial-preview__section'>
        <p className='tutorial-preview__text'>{details && details.topicsName}</p>
        <UnorderedList className='tutorial-preview__list'>
          <TopicsListItems topics={details && details.topicList} />
        </UnorderedList>
      </div>
      <div className='tutorial-preview__section'>
        <p className='tutorial-preview__text'>{details && details.outcomesName}</p>
        <UnorderedList className='tutorial-preview__list'>
          <OutcomesListItems outcomes={details && details.outcomeList} />
        </UnorderedList>
      </div>
      <div className='tutorial-preview__section'>
        <p className='tutorial-preview__text-header'>
          {
            summary && summary.overviewName && (
              'OVERVIEW'
            )
          }
        </p>
        {
          summary && summary.sections && summary.sections.map((item, index) => (
            <p key={`item-section:${index}`} className='tutorial-preview__text'>
              {item && item.section}
            </p>
          ))
        }
      </div>
    </div>
  )
}
