/*
This javascript file is for the main tutorials page which lists all the tutorials and has promoted tutoroials at the top of the page.
The search capability is also in this file. It uses IdeFilter.
*/

import 'whatwg-fetch'
import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import {
  IdePageHeader,
  IdePageContent,
  IdeSlideOverPanel
} from '@hybrid-cloud/ide-components'
// cp4i update
import { IdeFilter } from '@hybrid-cloud/ide-components/es/components/IdeFilter'
import { Time16 } from '@carbon/icons-react'
import { ClickableTile } from 'carbon-components-react'
import { ListPanelItem } from '../components/ListPanelItem'
import { Tutorials } from '../components/Tutorials.js'

import { LayoutNav } from '../layouts/LayoutNav'

import repoMetadata from '../repo_metadata.json'
import allTutorials from '../tutorials_data.json'

import './TutorialsPage.scss'

const TutorialsListItems = ({ search, sort, handleClick, tutorialsData }) => {
  const tutorials = (tutorialsData && tutorialsData.categories[0].tutorials) || repoMetadata.categories[0].tutorials.slice()
  const filterRegex = new RegExp(search, 'i')
  const filterFunc = (item) => (
    filterRegex.exec(item.name) !== null ||
    filterRegex.exec(item.tags) !== null ||
    filterRegex.exec(item.category) !== null
  )

  const list = tutorials.filter(filterFunc)

  const listDom = []

  let updatedList = list

  if (!search) {
    let removedItems = list.splice(0, 3)
    updatedList = list.concat(removedItems)
  }

  for (const item of updatedList) {
    const _tags = (item.tags && item.tags.split(';')) || []
    const _categories = (item.category && item.category.split(',')) || []
    listDom.push(
      <ListPanelItem
        name={item.name}
        desc={item.shortDesc}
        detailsLink={item.detailsURL}
        tags={_tags.concat(_categories)}
        key={item.name}
        handleLinkClick={(preview) => handleClick(item.name, preview)}
        hasButtons
        tutorialData={item}
      />
    )
  }

  return listDom
}

const PromotedTutorial = ({ name, image, durationMins, handleClick }) => {
  const imgSource = process.env.PUBLIC_URL + '/static_files/' + image + '.svg'
  return (
    <div className='promoted-tutorial__container bx--col-sm-4 bx--col-md-2 bx--col-lg-4 bx--col-xlg-4 bx--col-max-4'>
      <ClickableTile
        handleClick={() => handleClick(name)}
        className='promoted-tutorial__tile'
        tabIndex='0'
      >
        <p className='promoted-tutorial__text'>{name}</p>
        <div className='promoted-tutorial__lower'>
          <img src={imgSource} className='promoted-tutorial__img' alt='Tutorial' />
          <div className='promoted-tutorial__duration'>
            <div className='promoted-tutorial__lower-left'>
              <Time16
                className='promoted-tutorial__duration-img'
                aria-label='Timing indicator'
              />
              <span className='promoted-tutorial__duration-text'>
                {durationMins}
              </span>
            </div>
          </div>
        </div>
      </ClickableTile>
    </div>
  )
}

const PromotedTutorials = ({ handleClick, tutorialsData }) => {
  const list = []
  const tutorials = (tutorialsData && tutorialsData.categories[0].tutorials) || repoMetadata.categories[0].tutorials.slice()

  for (let i = 0; i < 3; i++) {
    const tutorialDuration = allTutorials[tutorials[i].name] && allTutorials[tutorials[i].name].tutorial._common.durationTime
    tutorials[i].durationMins = tutorialDuration
    if (tutorials[i].name.toLowerCase().includes('integration')) {
      tutorials[i].image = 'integration-server'
    } else if (tutorials[i].name.toLowerCase().includes('message')) {
      tutorials[i].image = 'message-flow'
    } else {
      tutorials[i].image = 'rest-api'
    }
    list.push(tutorials[i])
  }

  const listDom = []
  for (const item of list) {
    listDom.push(
      <PromotedTutorial
        name={item.name}
        image={item.image}
        key={item.name}
        className='promoted-tutorial'
        handleClick={handleClick}
        durationMins={item.durationMins}
      />
    )
  }

  return listDom
}

const filterOptions = [
  {
    label: 'Tags',
    options: []
  }
]

const initFilterOptions = () => {
  const tutorials = repoMetadata.categories[0].tutorials.slice()
  const allTagOptions = []
  const types = [
    'red',
    'magenta',
    'purple',
    'blue',
    'cyan',
    'teal',
    'green',
    'gray',
    'cool-gray',
    'warm-gray'
  ]
  const allTags = new Set()

  for (const item of tutorials) {
    const tags = (item.tags && item.tags.split(';')) || []
    const categories = (item.category && item.category.split(',')) || []

    for (const tag of tags) {
      allTags.add(tag)
    }

    for (const cat of categories) {
      allTags.add(cat)
    }
  }

  for (const tag of allTags) {
    allTagOptions.push({
      type: types[Math.floor(Math.random() * types.length)],
      value: tag,
      label: tag
    })
  }

  filterOptions.push({
    label: 'Tags',
    options: allTagOptions
  })
}
initFilterOptions()

export const TutorialsPage = (route) => {
  const history = useHistory()
  const [search, setSearch] = useState('')
  const [panelOpen, setPanelOpen] = useState(false)
  const [tutorialDetails, setTutorialDetails] = useState({})
  const [preview, setPreview] = useState(true)

  const ideNavigationContent = document.getElementsByClassName('IdeNavigation--content')

  const handleStartTutorial = (tutorialName) => {
    console.log(`Link click for '${tutorialName}'`)
    // Uncomment next line for local development, so that we can navigate to a specific tutorial by clicking start
    // history.push(`/tutorials/${tutorialName}${route.location.search}`)

    // Re-enables scrolling of main content on IE
    ideNavigationContent[0].style['overflow'] = 'auto'
  }

  // This causes the overview to be shown on the right hand side.
  const handleTutorialClick = (tutorialName) => {
    setPanelOpen(true)
    setTutorialDetails({ title: tutorialName })

    // Disables scrolling of main content on IE
    ideNavigationContent[0].style['overflow'] = 'hidden'
  }

  const handleClose = () => {
    setPanelOpen(false)
    setTutorialDetails({})

    // Re-enables scrolling of main content on IE
    ideNavigationContent[0].style['overflow'] = 'auto'
  }

  const handleFilterChange = (val) => {
    if (val) {
      const valMap = val.map((item) => item.value)
      setSearch(`((${valMap.join(")|(")}))`)
    } else {
      setSearch('')
    }
  }

  // Fetches latest repo_metadata from public Github
  const [tutorialsData, setTutorialsData] = useState(null)
  useEffect(() => {
    const repoMetadataUrl = 'https://ot4i.github.io/ace-tutorials/v12/en/repo_metadata.json'
    const fetchTutorialsData = async () => {
      const response = await fetch(repoMetadataUrl, {
        headers: new Headers({
          Accept: 'application/vnd.github.v3+json'
        }),
        method: 'GET',
        mode: 'cors',
        credentials: 'omit' // Useful for including session ID (and, IIRC, authorization headers)
      })

      const responseData = await response.json()
      if (responseData) {
        setTutorialsData(responseData)
      }
    }
    // Uncomment next line if you want to use a local dev version of repo_metadata.json
    fetchTutorialsData()
  },[])

  return (
    <LayoutNav>
      <IdePageHeader heading='Tutorials' />
      <IdePageContent>
        <div className='tutorials'>
          <IdeFilter
            options={filterOptions}
            onChange={handleFilterChange}
            placeholderText='What are you looking for today?'
            searchForText='Search for'
          />
          <div className='tutorials-page__section'>
            {
              search.length === 0 && (
                <>
                  <ul className='tutorials__promoted-list'>
                    <PromotedTutorials
                      handleClick={handleTutorialClick}
                      tutorialsData={tutorialsData}
                    />
                  </ul>
                </>
              )
            }
            <ul className='tutorials__list'>
              <TutorialsListItems
                search={search}
                handleClick={handleTutorialClick}
                tutorialsData={tutorialsData}
              />
            </ul>
          </div>
        </div>
      </IdePageContent>
      <IdeSlideOverPanel
        open={panelOpen}
        onOverlayClose={handleClose}
        onClose={handleClose}
        size='medium'
        title={tutorialDetails.title}
      >
        <Tutorials
          name={tutorialDetails.title}
          preview={preview}
          handleLinkClick={handleStartTutorial}
          tutorialsData={tutorialsData}
        />
      </IdeSlideOverPanel>
    </LayoutNav>
  )
}
